:root {
    --yellow: #ffc600;
    --black: #272727;
}

html {
    font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
        "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    /* font-weight: 900; */
    font-size: 16px;
    color: var(--black);
    /* text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07); */
}

html * {
    font-family: "Roboto", sans-serif;
    color: bisque;
}

input {
    color: #000000;
    font-weight: bolder;
}

html {
    scroll-behavior: smooth !important;
}

.stylish {
    font-family: "Lobster", cursive;
}

body {
    background-image: linear-gradient(110deg, #0e0918, #660033);
    /* margin: 5px; */
    background-position: fixed;
}

label {
    display: flex;
    align-items: center;
    margin: 0px;
    font-weight: bolder;
    font-size: larger;
}

.btn {
    font-weight: bolder;
    font-size: 105%;
}

.review {
    /* border-color: darkslategray;
    border-style: solid;
    border-left-width: 2px;
    border-right-width: 2px;
    border-top-width: 1px;
    border-bottom-width: 1px; */
    box-shadow: 0px 0px 10px 6px #004d99;
    border-radius: 10px;
}

.review:hover {
    box-shadow: 0px 0px 10px 6px #008000;
}

span {
    background-color: darkgrey;
    padding: 0.3rem;
    font-size: 0.8em;
    font-family: monospace;
}

p {
    line-height: 1.6em;
}

.tools {
    display: flex;
    justify-content: space-between;
    width: 400px;
}

input[type="number"] {
    width: 50px;
}
